/** @jsx React.DOM */
var Component = React.createClass({displayName: 'Component',

  getDefaultProps: function () {
    return {
      authorData: {
        gravatarHash: 'undefined'
      }
    }
  },

  getAvatarUrl: function () {
    return '//www.gravatar.com/avatar/' + this.props.authorData.gravatarHash + '?d=retro&s=200';
  },

  render: function () {
    return (
      React.DOM.section({className: "media-block media-block--cv-style"}, 
        React.DOM.img({src: this.getAvatarUrl(), className: "media-block__image", width: "100"}), 
        React.DOM.h2({className: "media-block__title"}, React.DOM.span({className: "badge badge--right-tip"}, "Author"), " ", this.props.authorData.displayName), 
        React.DOM.h2({className: "media-block__title"}, React.DOM.span({className: "badge badge--right-tip"}, "Occupation"), " ", this.props.authorData.occupation), 
        React.DOM.h2({className: "media-block__title"}, React.DOM.span({className: "badge badge--right-tip"}, "Specialties"), " ", this.props.authorData.specialties), 
        React.DOM.hr(null)
        /* List of contributions */
      )
    );
  }

});

module.exports = Component;
